.container input {
    width: 400px;
    height: 50px;
    box-sizing: border-box;
    padding-left: 20px;
    font-size: 20px;
    outline: none;
    border-radius: 6px;
    border: 1px solid;
    background-color: var(--dark-slate-gray);
    color: white;
}

.error {
    border: 2px solid var(--sizzling-red) !important;
}